
@use 'sass:color';

// ========== 配色系统（与念头植入保持一致）==========
$c-bg: #0d0d0d;
$c-panel: rgba(18, 18, 18, 0.98);
$c-gold: #d4af37;
$c-gold-dim: #8a7326;
$c-red: #8a1c1c;
$c-text: #e0e0e0;
$c-text-sub: #888;
$font-serif: 'Noto Serif SC', 'Songti SC', 'STSong', serif;
$font-sans: 'Roboto', sans-serif;

// ========== 容器 ==========
.options-container {
  position: relative;
  width: 100%;
  font-family: $font-sans;
  color: $c-text;
  background: $c-bg;
  overflow: hidden;
}

// ========== 背景纹理 ==========
.bg-pattern {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image:
    // 暗红光晕
    radial-gradient(ellipse at 30% 0%, rgba($c-red, 0.1), transparent 50%),
    radial-gradient(ellipse at 70% 100%, rgba($c-red, 0.08), transparent 50%),
    // 金色点缀
    radial-gradient(circle at 50% 50%, rgba($c-gold, 0.03), transparent 60%),
    // 织物暗纹
    repeating-linear-gradient(
      45deg,
      transparent,
      transparent 1px,
      rgba(255, 255, 255, 0.02) 1px,
      rgba(255, 255, 255, 0.02) 2px
    ),
    repeating-linear-gradient(
      -45deg,
      transparent,
      transparent 1px,
      rgba(255, 255, 255, 0.02) 1px,
      rgba(255, 255, 255, 0.02) 2px
    );
  z-index: 0;
}

// ========== 主面板 ==========
.options-panel {
  position: relative;
  z-index: 1;
  padding: 16px 20px;
  background: $c-panel;
  border: 1px solid rgba($c-gold, 0.15);
  border-bottom: 1px dashed rgba($c-gold, 0.1);
  backdrop-filter: blur(5px);
}

// ========== 顶部装饰线 ==========
.panel-decor {
  position: absolute;
  top: 6px;
  left: 15px;
  right: 15px;
  height: 1px;
  background: linear-gradient(90deg, transparent, $c-gold, transparent);
  opacity: 0.3;
}

// ========== 标题 ==========
.options-header {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
  margin-bottom: 14px;

  .title {
    color: $c-gold;
    font-size: 12px;
    font-family: $font-serif;
    letter-spacing: 3px;
    text-shadow: 0 0 8px rgba($c-gold, 0.3);
  }

  .decor-line {
    height: 1px;
    width: 50px;
    background: linear-gradient(90deg, transparent, $c-gold-dim, transparent);
    opacity: 0.6;
  }
}

// ========== 选项网格 ==========
.options-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 8px;

  // 中等宽度：两列
  @media (min-width: 400px) {
    grid-template-columns: 1fr 1fr;
    gap: 6px;
  }

  // 宽屏时 6+ 选项使用四列
  &.grid-4col {
    @media (min-width: 700px) {
      grid-template-columns: repeat(4, 1fr);
      gap: 6px;
    }
  }
}

// ========== 选项按钮 ==========
.option-btn {
  position: relative;
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 10px 12px;
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba($c-gold, 0.12);
  border-left: 2px solid $c-gold-dim;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  animation: fadeSlideIn 0.5s ease forwards;
  opacity: 0;

  // 四列布局时更紧凑
  .grid-4col & {
    padding: 8px 10px;
    gap: 6px;
  }

  &:hover {
    background: rgba($c-gold, 0.08);
    border-color: rgba($c-gold, 0.4);
    border-left-color: $c-gold;
    transform: translateX(3px);
    box-shadow:
      0 0 15px rgba($c-gold, 0.1),
      inset 0 0 20px rgba($c-gold, 0.03);

    .option-icon {
      color: $c-gold;
      text-shadow: 0 0 8px $c-gold;
    }

    .option-text {
      color: #fff;
    }
  }

  &.sending {
    background: rgba($c-red, 0.4);
    border-color: $c-red;
    border-left-color: #ff4500;

    .option-icon {
      color: #ff6b6b;
    }

    .option-text {
      color: #ffcccc;
    }
  }
}

.option-icon {
  font-size: 10px;
  color: $c-gold-dim;
  transition: all 0.3s;
}

.option-text {
  flex: 1;
  font-size: 13px;
  color: $c-text;
  font-family: $font-serif;
  transition: color 0.3s;
  line-height: 1.4;

  // 四列布局时字体稍小
  .grid-4col & {
    font-size: 12px;
  }
}

// ========== 动画 ==========
@keyframes fadeSlideIn {
  from {
    opacity: 0;
    transform: translateY(8px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
